$color-background: lightblue;

body{
  background: $color-background;
}

.update{
  fill:#8a8a8a;
}
#chart{
  position: fixed;
  top:0;
}
#content{margin-top: 100vh;}

.tooltip {
  position: absolute;
  text-align: left;
  display: inline-block;
  padding: 3px 8px;
  font-size: 10px;
  background: rgba(248, 252, 246, 0.5);
  border-radius: 2px;
  pointer-events: none;
  h3{
    margin: 0;
    font-family: Arial;
    font-style: normal;
    font-weight: bold;
    font-size: 32px;
    line-height: 110%;
  }
  p{
    font-size: 20px;
  }

}

path{
  paint-order: stroke;
  stroke:white;
  stroke-width: 1px;
  stroke-opacity: 1;
  &.hovered{
    stroke: black;
    stroke-width: 1.5px;
  }
}

.ui.table td {
  padding: 0.1em;
}